<template>
  <!-- バナーだったりを表示する領域 -->
  <transition name="fade" key="views" mode="out-in" appear>
    <!-- !!!!!!!!!!テスト終了時は!thisをthisに変更!!!!!!!!!!!!! -->
    <div class="campaign-area"
    v-if="this.$route.path.match(/\/$/)">
      <!-- バナーはv-forで回す -->
      <ul>
        <li v-for="banner in banners" :key="banner">
          <img :src="banner" alt="banner">
        </li>
      </ul>
    </div>
  </transition>
</template>

<script>

export default {
  name: 'CampaignArea',
  // バナーのURLを格納
  data:()=>({
    banners: [
      require('@/assets/kari-banner1.svg'),
      require('@/assets/kari-banner2.svg'),
      require('@/assets/kari-banner3.svg'),
    ]
  })
}
</script>

<style scoped>
  .campaign-area{
    position: fixed;
    left: 6vw;
    width: auto;
    max-width: 20vw;
    padding: 10vh 0 0 0;
    height: auto;
  }

  ul{
    width: 100%;
    height: auto;
    list-style: none;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }

  li{
    height: auto;
    padding: 3vh 0;
  }


  img{
    width: 100%;
    max-width: 15vw;
    height: auto;
  }

  @media screen and (max-width:600px){
  .campaign-area{
    width: 100%;
    margin-right: 0;
    position: relative;
    left: 0;
    max-width: none;
    align-items: center;
  }

    img{
    max-width: none;
  }
}
</style>
