<template>
  <div class="in-views introduction">
    <article>
      <transition name="intoleft" appear>
        <h2 class="heading2">
          <transition name="intoleft" mode="out-in" appear>
            <p class="sub">イントロダクション</p>
          </transition>
          <p>Introduction</p>
        </h2>
      </transition>

      <!-- ここからスライド -->
      <div id="slide-area" ref="slideArea">
      <hooper
        id="intro-hooper"
        :settings="hooperSettings"
      >
        <!-- 1ページ目 -->
        <slide class="hooper-slide">
          <transition name="intoleft" mode="out-in" appear>
            <div class="slide-text-area">
              <p class="slide-text">
                吾輩は猫である。名前はまだ無い。どこで生れたかとんと見当がつかぬ。
                何でも薄暗いじめじめした所でニャーニャー泣いていた事だけは記憶している。
                吾輩はここで始めて人間というものを見た。
              </p>
            </div>
          </transition>
          <transition name="intobottom" appear>
            <img
              class="slide-img"
              src="@/assets/pic_introduction_01.png"
            >
          </transition>
        </slide>

        <!-- 2ページ目 -->
        <slide class="hooper-slide">
          <div class="slide-text-area">
            <p class="slide-text">
              木曾路はすべて山の中である。あるところは岨づたいに行く崖の道であり、
              あるところは数十間の深さに臨む木曾川の岸であり、
              あるところは山の尾をめぐる谷の入り口である。
              一筋の街道はこの深い森林地帯を貫いていた。
              東ざかいの桜沢から、西の十曲峠まで、木曾十一宿はこの街道に添うて、
              二十二里余にわたる長い谿谷の間に散在していた。
              道路の位置も幾たびか改まったもので、古道はいつのまにか
              深い山間に埋もれた。名高い桟も、蔦のかずらを頼みにしたような
              危い場処ではなくなって、徳川時代の末にはすでに渡ることのできる橋であった。
              新規に新規にとできた道はだんだん谷の下の方の位置へと降って来た。
              道の狭いところには、木を伐って並べ、藤づるでからめ、それで街道の狭いのを補った。
              長い間にこの木曾路に起こって来た変化は、いくらかずつでも嶮岨な山坂の多いところを歩きよくした。
              そのかわり、大雨ごとにやって来る河水の氾濫が旅行を困難にする
            </p>
          </div>
          <img
            class="slide-img"
            src="@/assets/pic_introduction_02.png"
          >
        </slide>

        <!-- 3ページ目 -->

        <slide class="hooper-slide">
          <div class="slide-text-area">
            <p class="slide-text">
              しかし、私はあなたに喜びを非難し、痛みを賞賛するというこの誤った考えが
              どのように生まれたかを説明しなければなりません、そして私はあなたに
              システムの完全な説明を与え、真実の偉大な探検家、人間の幸福のマスタービルダー。
            </p>
          </div>
          <img
            src="@/assets/pic_introduction_03.png"
            class="slide-img"
          >
        </slide>
      <hooper-pagination slot="hooper-addons"/>
      <hooper-navigation slot="hooper-addons"/>
      </hooper>
      </div>
    </article>
  </div>
</template>

<script>
import {
  Hooper,
  Slide,
  Pagination as HooperPagination,
  Navigation as HooperNavigation
} from 'hooper';


export default {
  name: 'Introduction',
  data(){
    return{
      name:"",
      hooperSettings:{
        itemToShow: 1,
        infiniteScroll: true,
        wheelControl: false,
      }
    }
  },
  mounted(){
    this.$nextTick(()=>{
      // views-wrapクラスを格納
      const setViewStyle = document.getElementsByClassName('views-wrap')[0];

      // views-wrapクラスのoffsetHeightを格納
      const viewAreaHeight = document.getElementsByClassName('views-wrap')[0].offsetHeight;
      
      // views-wrapクラスのCSSを格納
      const getViewStyle = window.getComputedStyle(setViewStyle);
      
      // 上記で取得したCSSからmargin-topを取得
      const slideMargin = getViewStyle.getPropertyValue('margin-top');
      
      // 取得したmargin-topの値を数値化
      const slideMarginMath = Number(slideMargin.replace(/px/g, ''));
      
      // global-navクラスの高さを格納
      const globalNavHeight = document.getElementsByClassName('global-nav')[0].offsetHeight;

      // views-wrapの高さ+global-navの高さ+views-wrapのmargin-top*2の数値を格納
      const slideAreaHeight = viewAreaHeight + globalNavHeight + (slideMarginMath * 2);
      
      // slide-areaの高さをslideAreaHeightに設定
      document.getElementById('slide-area').style.height = slideAreaHeight + 'px';
    });
  },
  components:{
    Hooper,
    Slide,
    HooperPagination,
    HooperNavigation,
  },
}
</script>

<style scoped>
/* hooperそのもののcssはhooper.cssに記述 */
.introduction{
  margin-top: 0;
}



#slide-area{
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

#intro-hooper{
  width: auto;
  height: 100%;
}

.hopper-pagination{
  z-index: 4;
  position: absolute;
  top: 50%;
}

.slide-img{
  object-fit: cover;
  position: absolute;
  top: 0;
  width: auto;
  height: 100%;
  z-index: 2;
}

.slide-text-area{
  width: 50vw;
  height: auto;
  position: absolute;
  z-index: 3;
  /* leftかrightで設定すると要素が消えるのでtransform */
  transform: translate(6vw, 40vh);
}

.slide-text{
  font-size: 2.5vw;
  color: #fff;
  filter: drop-shadow(2px 2px 5px black);
}

.icon{
  width: 100%;
  height: 100%;
}

</style>
